import React from 'react';

import {lazy} from 'react';

// project import
import Loadable from 'components/Loadable';
import MainLayout from 'layout/MainLayout';
const DefaultPage = Loadable(lazy(() => import('pages/DefaultPage')));

// [1 ] (Board)   
const Board = Loadable(lazy(() => import('pages/board/Board')));

// [2  ] Todo:  (CreatePost)   
const CreatePost = Loadable(lazy(() => import('pages/board/CreatePost'))); // .

// [3  ] Todo:  (Post)   
const Post = Loadable(lazy(() => import('pages/board/Post'))); // .

// [4  ] Todo:  (UpdatePost)   
const UpdatePost = Loadable(lazy(() => import('pages/board/UpdatePost'))); // .

// ==============================|| MAIN ROUTING ||============================== //

const MainRoutes = {
	path: '/',
	element: <MainLayout />,
	children: [
		{
			path: '',
			element: <DefaultPage />,
		},
		// [1 ] (Board)  
		{
			path: 'board',
			element: <Board />,
		},

		// [2  ] Todo:  (CreatePost)  ('post/write')
		{
			// .
			path: 'post/write',
			element: <CreatePost />,
		},

		// [3  ]  (Post)  
		{
			path: 'post/:id',
			element: <Post />, // .
		},
		// [4  ] Todo:  (UpdatePost)  ('post/update')
		{
			path: 'post/update',
			element: <UpdatePost />, // .
		}, // .
	],
};

export default MainRoutes;
